.input-range-container {
}

/* In the case of the double slider, each individual slider has it's width set to 100% of the parent element. Therefore, in order to set a fixed width, it is recommended to set it on the parent element and not override the width of the range slider. This is to ensure the flexibility of the component. */
.input-range-container,
.input-range {
  width: 100%;
  height: 20px;
}

.input-range,
.input-range:hover,
.input-range:focus {
  box-shadow: none;
}

.input-range {
  -webkit-appearance: none;
  background-color: transparent;
  padding: 0;
  overflow: visible;
  pointer-events: none;
  height: 20px;
  border: 0;
}

.input-range::-moz-focus-outer {
  border: 0;
}

.input-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border: none;
  background-color: white;
  box-shadow: 0 0 0 2px rgba(33, 34, 36, 0.07);
  cursor: pointer;
  pointer-events: all;
  z-index: 2;
  position: relative;
}

.input-range::-moz-range-track {
  background: transparent;
}

.input-range::-moz-range-thumb {
  height: 20px;
  width: 20px;
  border: none;
  background-color: white;
  box-shadow: 0 0 0 2px rgba(33, 34, 36, 0.07);
  cursor: pointer;
  pointer-events: all;
  z-index: 2;
  position: relative;
  transform: scale(1);
}

.input-range::-ms-track {
  background-color: transparent;
  border-color: transparent;
  color: transparent;
}

.input-range::-ms-fill-lower {
  background-color: transparent;
}

.input-range::-ms-thumb {
  height: 20px;
  width: 20px;
  border: none;
  background-color: white;
  box-shadow: 0 0 0 2px rgba(33, 34, 36, 0.07);
  cursor: pointer;
  pointer-events: all;
  z-index: 2;
  position: relative;
}

.input-range:disabled,
.input-range:disabled:hover {
  cursor: not-allowed;
  box-shadow: none;
  border: 0;
  background-color: transparent;
}

.input-range:disabled::-webkit-slider-thumb,
.input-range:disabled:hover::-webkit-slider-thumb {
  cursor: not-allowed;
}

.input-range:disabled::-moz-range-thumb,
.input-range:disabled:hover::-moz-range-thumb {
  cursor: not-allowed;
}

.input-range:disabled::-ms-thumb,
.input-range:disabled:hover::-ms-thumb {
  cursor: not-allowed;
}

.input-range:disabled ~ .input-range__track,
.input-range:disabled:hover ~ .input-range__track {
  cursor: not-allowed;
  background-color: rgb(42, 43, 44);
}

.input-range:disabled ~ .input-range__progress,
.input-range:disabled:hover ~ .input-range__progress {
  cursor: not-allowed;
  background-color: #dcdee1;
}

.slider-thumb {
  height: 20px;
  width: 20px;
  border: none;
  background-color: white;
  box-shadow: 0 0 0 2px rgba(33, 34, 36, 0.07);
  cursor: pointer;
  pointer-events: all;
  z-index: 2;
  position: relative;
  z-index: 2;
}

.slider-thumb--first {
  margin-left: -16px;
}

.slider-thumb--second {
  margin-left: -20px;
}

.input-range--first {
  position: absolute;
}

.input-range--second {
  position: relative;
}

.input-range__track,
.input-range__progress {
  position: absolute;
  height: 100%;
  z-index: 0;
}

.input-range__track:hover,
.input-range__progress:hover {
  cursor: pointer;
}

.input-range__track {
  background-color: rgb(22, 23, 23);
  left: 0;
  right: 0;
}

.input-range__track:hover {
  cursor: pointer;
}

.input-range__progress {
  background-color: rgb(51, 51, 51);
}

.input-range-labels-container {
  display: none;
  justify-content: space-between;
}

.input-range-label {
  font-weight: bold;
}

.input-range-label--current-value {
  color: rgb(51, 51, 51);
  text-align: center;
  flex: 2;
}

.input-range-label:first-child {
  text-align: left;
}

.input-range-label:last-child {
  text-align: right;
}
